.modal-window {
  position: absolute;
  top: calc(50% - 250px);
  right: 0;
  bottom: 0;
  left: 0;
  height: max-content;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-bottom: 46px;
  padding-top: 28px;
  padding-left: 30px;
  padding-right: 30px;
  background-color: $color-white;
  box-shadow: 0 5px 10px rgba($color-black, 0.25);

  &--visible {
    display: block;
    z-index: 5;
  }

  &--hidden {
    display: none;
  }

  @media (min-width: $tablet-width) {
    padding-top: 37px;
    width: 566px;
  }
}

.modal-window__title {
  margin-top: 0;
  margin-bottom: 14px;
  padding-left: 55px;
  padding-right: 55px;
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: $color-black;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    text-align: center;
  }
}

.modal-window__subtitle {
  display: block;
  margin-bottom: 21px;
  font-size: 15px;
  line-height: 30px;
  text-align: center;
}

.modal-window__button {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  padding-top: 18px;
  padding-bottom: 18px;
  font-weight: 700;
  font-size: 17px;
  line-height: 16px;
  color: $color-white;
  background-color: $color-green-200;
  border: none;

  @media (min-width: $tablet-width) {
    width: auto;
    min-width: 350px;
  }
}
