.user-list {
  list-style: none;
  margin: 0;
  padding: 0;

  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
  }

  @media (min-width: $desktop-width) {
    justify-content: flex-end;
  }
}

.user-list__item {
  border-bottom: 1px solid $color-gray-100;

  @media (min-width: $tablet-width) {
    display: flex;
    text-align: end;
    border: none;
    padding: 0;
  }
}

.user-list__link {
  display: block;
  position: relative;
  padding-top: 23px;
  padding-bottom: 26px;
  padding-right: 25px;
  padding-left: 76px;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: $color-black;
  text-decoration: none;

  &:hover {
    color: rgba($color-black, 0.6);

    .user-list__svg {
      opacity: 1;
      color: $color-green-200;
    }
  }

  &:active {
    .user-list__svg {
      opacity: 0.3;
    }
  }

  @media (min-width: $tablet-width) {
    padding-top: 33px;
    padding-left: 32px;
    padding-right: 0;
    padding-bottom: 32px;

    &--search {
      min-width: 83px;
      border-left: 1px solid $color-gray-200;
      border-right: 1px solid $color-gray-200;

      .user-list__svg {
        left: 32px;
      }
    }

    &--basket {
      padding-left: 83px;
    }
  }

  @media (min-width: $desktop-width) {
    padding-left: 61px;
  }
}

.user-list__svg {
  position: absolute;
  left: 32px;
  top: 25px;
  opacity: 0.2;

  @media (min-width: $tablet-width) {
    left: 30px;
    top: 35px;
  }

  @media (min-width: $desktop-width) {
    left: 25px;
  }
}

.user-list__search {
  @media (min-width: $tablet-width) {
    text-align: center;
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}
