.radio-button {
  position: relative;
  display: block; }

.radio-button__label {
  display: block;
  padding-top: 11px;
  padding-bottom: 11px;
  padding-left: 60px;
  font-weight: 700;
  font-size: 17px;
  line-height: 18px;
  color: #000000; }
  .radio-button__label::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 42px;
    height: 42px;
    border: 2px solid #444444;
    border-radius: 50%; }
  .radio-button__label::before {
    content: "";
    display: none;
    position: absolute;
    top: 12px;
    left: 12px;
    width: 18px;
    height: 18px;
    background-color: #62d1ba;
    border-radius: 50%; }
  .radio-button__label:hover::after {
    border-color: #62d1ba; }
  .radio-button__label:active::after {
    border-color: rgba(0, 0, 0, 0.3); }

.radio-button__input {
  position: absolute;
  width: 42px;
  height: 42px;
  margin: 0;
  opacity: 0; }
  @media (min-width: 768px) {
    .radio-button__input {
      height: 52px;
      width: 52px; } }

.radio-button__input:focus + .radio-button__label {
  outline: 2px solid #000000;
  outline-offset: 2px; }
  .radio-button__input:focus + .radio-button__label::after {
    border-color: #62d1ba; }

.radio-button__input:checked + .radio-button__label::before {
  display: block; }

.checkbox {
